<template>
  <v-container>
    <v-layout row wrap class="mb-3">
      <v-flex xs12 sm6 class="text-xs-center text-sm-right">
        <v-btn large router to="/meetups" class="secondary">Explore Meetups</v-btn>
      </v-flex>
      <v-flex xs12 sm6 class="text-xs-center text-sm-left">
        <v-btn large router to="/meetup/new" class="secondary">Organaze Meetups</v-btn>
      </v-flex>
    </v-layout>

    <v-layout row wrap>
      <v-flex xs12>
        <v-carousel>
          <v-carousel-item 
            v-for="meetup in meetups"
            :src="meetup.imageUrl" 
            :key="meetup.id"
            @click="onLoadMeetup(meetup.id)"
            class="text-sm-center text-xs-center">
            <div class="title">
              {{ meetup.title }}
            </div>
          </v-carousel-item>
        </v-carousel>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
  export default {
    data () {
      return {
        meetups: [
          {
            imageUrl: 'http://www.bb.com.br/pbb/pagina-inicial/atendimento/bb-no-mundo/img.ImgWriter?codigo=42592&origem=CCI',
            id: 'xsjajdsijadiajsi',
            title: 'my meetup'},
          {
            imageUrl: 'https://media.timeout.com/images/103444978/image.jpg',
            id: 'xsjddsajdsijadiajsi',
            title: 'my metttttt tetup'},
          {
            imageUrl: 'http://www.bb.com.br/pbb/pagina-inicial/atendimento/bb-no-mundo/img.ImgWriter?codigo=42592&origem=CCI',
            id: 'xsjaddjdsijadiajsi',
            title: 'O problema aqui é que você quer ter o menu de topo (#header) com position: fixed e isso faz com que o CSS considere 100% da largura maxima do ecrã.'}
        ]
      }
    },
    methods: {
      onLoadMeetup (id) {
        console.log('olallll ' + id)
        this.$router.push('/meetup/' + id)
      }
    }
  }
</script>

<style scoped>
  .title{
    position: relative;
    top: 410px;
    background-color: rgba(0, 0, 0, 0.5);
    color: white;
    padding: 10px 20px;
  }
</style>



